import React, { Component } from 'react';
  
export default class ContentItem extends Component {

  render() {
    let collapseClass = "collapse"
    if(this.props.question.state) {
        collapseClass += " show"
    }

    return (
        <>
            <div className={this.props.question.tag}>
                <h2 className="content-link">
                    <a  data-toggle="collapse" href={"#" + this.props.question.tag + "_body"} aria-expanded={this.props.question.state} aria-controls={this.props.question.tag + "_body"}>
                        Q. {this.props.question.question}  <i className="icon icon-minimal-down" ></i>
                    </a>
                </h2>
            </div>
            <div id={this.props.question.tag + "_body"} className={collapseClass} aria-labelledby="{this.props.question.tag}" data-parent="#faqsection">
                <div className="card-body">{this.props.question.answer}</div>
            </div>
        </>
    );
  }
};