import React, { Component } from 'react';

export default class Contact extends Component {

_show_result(data) {
    // TODO: check the server response before showing success
    this.setState({ step: "1" });
}

_get_request_option(formJson) {
    return {
      method: 'POST',
      headers: { 'Content-Type': 'application/json', 'Accept': 'application/json, text/plain, */*' },
      body: formJson,
    };
}

submit_request(e) {
    e.preventDefault();
    this.setState({ step: "-1" });
    let formData = new FormData(e.target);
    let obj = Object.fromEntries(formData);
    obj.name = "Callback Request";
    obj.email_id = "callback@dentostack.com";
    let formJson = JSON.stringify(obj);
    if(!this.state.DEBUG) {
        fetch(this.state.request_callback, this._get_request_option(formJson))
        .then(response => response.json())
        .then(data => this._show_result(data))
        .catch( err => console.log(err));
    } else {
        this._show_result();
    }
  }

  state = {
    step : "0",
    DEBUG: false,
    request_callback: "https://apiv1.dentostack.com/lead"
  }

  render() {
    return (
        <div className="footer-section" id="contact">
            <div className="container">
                <div className="footer-newsletter-section">
                    <div className="row justify-content-center">
                        <div className="col-lg-10 col-xl-8">
                            <div className="footer-newsletter-content">
                                <h2>Still have questions? We are happy to help you !!</h2>
                            </div>
                        </div>
                        <div className="col-lg-7 col-xl-6">
                            <div className="footer-newsletter-form">
                                {this.state.step === "-1" ?
                                <div className="articleCustom center" >
                                    <div className="spinner-border text-primary" role="status">
                                    <span className="sr-only">Loading...</span>
                                </div></div> : null}
                                {this.state.step === "0" ?
                                <form onSubmit={this.submit_request.bind(this)}>
                                        <div className="form-group">
                                            <input name="customer_number" type="phone" className="form-control" placeholder="Enter your contact number" autoComplete="off"/>
                                            <button className="submit-btn">Request Call Back</button>
                                        </div>
                                        <p className="form-text">Or give us a missed call at  <i className="icon icon-phone-2"></i> +91-8602293854 or <i className="icon icon-email-84"></i> care@dentostack.com</p>
                                </form> : null}
                                {this.state.step === "1" ?
                                <div className="articleCustom center" >
                                    <h3>You request has been submitted.</h3>
                                </div> : null}
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
  }
};