import React, { Component } from 'react';
  
export default class FooterCard extends Component {

  render() {
    return (
        <div className="col-xl-4 col-lg-5 col-margin">
            <div className="single-footer">
                <div className="footer-title logo">
                <img className="fulllogo" src={this.props.context.logo} alt="Dentostack" />
                </div>
                <div className="footer-text">
                    <p><b>Address :</b> {this.props.context.address}</p>
                    <p><b>Email :</b> {this.props.context.email}</p>
                </div>
            </div>
            <ul className="footer-social-list">
            {
                this.props.context.social_media.map((media, index) => (
                    <li key={index} >
                      <a href={media.url}>
                        <i className={media.icon}></i>
                      </a>
                    </li>
                ))
            }
            </ul>
        </div>
    );
  }
};