import React, { Component } from 'react';
  
export default class ContentItem extends Component {

  render() {
    return (
        <section className={this.props.context.classes_[0]}>
            <div className="container">
                <div className="row justify-content-center">
                    <div className={this.props.context.classes_[1]}>
                        <div className="content-img">
                            <img src={this.props.logo} alt="content" />
                        </div>
                    </div>
                    <div className={this.props.context.classes_[2]}>
                        <div className="content-right-content">
                            <h2>{this.props.context.title}</h2>
                            {
                                this.props.context.text.map((item, index) => (
                                    <p key={index}>{item}</p>
                                ))
                            }
                        </div>
                    </div>
                </div>
            </div>
        </section>
    );
  }
};