export const dynamicTitle =(data={}) =>{
    let base = "Dentostack - A Modern Clinic Management suite"
    if( data.title ) {
        window.document.title = data.title + " | " + base;
    } else {
        window.document.title = base;
    }
};
    
export const setDescription = desc => {
    const el = document.querySelector("meta[name='description']");
    el.setAttribute('content',desc);
}