import React, { Component } from 'react';
import Navigation from '../components/Navigation';
import Footer from "../components/Footer";
import Registration from '../components/Registration';
import RegistrationResult from '../components/RegistartionResult';
import {dynamicTitle, setDescription} from '../components/Helper'

export default class Register extends Component {

    componentDidMount = () => {
        dynamicTitle( {
          title: 'Claim Free Trial'
        } );
        setDescription( "Register today and start using dentostack for FREE" )
      }

    _reset_field(dom_id, idx) {
        let field = document.getElementById(dom_id);
        let invalid = document.getElementsByClassName('invalid-feedback')[idx];
        let valid = document.getElementsByClassName('valid-feedback')[idx];
        field.setCustomValidity("")
        valid.style.display = 'none';
        invalid.style.display = 'none';
        field.classList.remove( 'is-valid' );
        field.classList.remove( 'is-invalid');
    }
    
      _update_field_status(dom_id, idx, is_valid) {
        let field = document.getElementById(dom_id);
        let invalid = document.getElementsByClassName('invalid-feedback')[idx];
        let valid = document.getElementsByClassName('valid-feedback')[idx];
        let config;
        if(is_valid) {
          config = ['block', 'none', 'is-valid', 'is-invalid', ''];
        } else {
          config = ['none', 'block', 'is-invalid', 'is-valid', 'invalid'];
        }
        valid.style.display = config[0];
        invalid.style.display = config[1];
        field.classList.add( config[2] );
        field.classList.remove( config[3] );
        field.setCustomValidity(config[4]);
    }

    _get_request_option(formJson) {
        return {
          method: 'POST',
          headers: { 'Content-Type': 'application/json', 'Accept': 'application/json, text/plain, */*' },
          body: formJson,
        };
    }


    _show_result(data) {
        // TODO: check the server response before showing success
        this.setState({ step: 1 });
    }


    validate_voucher(e) {
        let promo = document.getElementById("promo_code");
        let form = document.getElementById('voucher_code');
        form.classList.remove('was-validated');
        this._reset_field('promo_code', 5);
        if(promo.value.length !== 0) {
            if(!this.state.DEBUG) {
                let jsonData = JSON.stringify({ promo_code: promo.value } );
                fetch(this.state.voucherValidate, this._get_request_option(jsonData))
                .then(response => response.json())
                .then(data => {
                    this._update_field_status("promo_code", 5, !data.hasError);
                    document.getElementsByClassName('valid-feedback')[5].innerHTML = data.data;
                })
                .catch( err => console.log(err));
            } else {
                var dummyResponse = {
                    'hasError': false,
                    'data': 'You will get 3-month Super plan subscription for FREE.'
                }
                this._update_field_status("promo_code", 5, !dummyResponse.hasError);
                document.getElementsByClassName('valid-feedback')[5].innerHTML = dummyResponse.data;
            }
        }
    }

    _validate_form(e) {
        e.preventDefault();
        let is_valid = e.target.checkValidity();
        let text_boxes = Array.from(document.getElementsByClassName("form-group"));
        let checkbox =  Array.from(document.getElementsByClassName("form-check"));
        var inputs = text_boxes.concat(checkbox);
        for (var i = 0; i < inputs.length; i++) {
            inputs[i].classList.remove('was-validated');
            let is_optional = inputs[i].classList.contains('optional_field');
            let has_value = false;
            let input = inputs[i].querySelector('input');
            if(input) {
              has_value = input.value.length > 0;
            }
            if(! is_optional ||  has_value) {
              inputs[i].classList.add('was-validated');
            }
        }
        return is_valid;
    }

    register(e) {
        if(this._validate_form(e)) {
            this.setState({ step: "-1" });
            let formData = new FormData(e.target);
            let formJson = JSON.stringify(Object.fromEntries(formData));
            if(!this.state.DEBUG) {
                fetch(this.state.register, this._get_request_option(formJson))
                .then(response => response.json())
                .then(data => this._show_result(data))
                .catch( err => console.log(err));
            } else {
                let dummyResponse = {
                    'hasError':false,
                }
                this._show_result(dummyResponse);
            }
        }
    }

    state = {
        step : 0,
        DEBUG : false,
        titles: {
            "-1" : "Please wait...",
            0 : "Fill your details to start using Dentostack",
            1 : "Welcome to Dentostack !"
        },
        register: "https://apiv1.dentostack.com/lead",
        voucherValidate : "https://apiv1.dentostack.com/promo/validate",
    }

    render() {
        return (
            <>
                <Navigation />
                <div className="article-section">
                    <div className="container">
                        <div className="last article-section-inner">
                            <div className="col-lg-12 col-xl-12 form-header">
                                <h2>{this.state.titles[this.state.step]}</h2>
                            </div>
                            <div className="offset-lg-2 offset-xl-2 col-lg-8 col-xl-8">
                            {this.state.step === "-1" ?
                                <div className="articleCustom center" >
                                    <div className="spinner-border text-primary" role="status">
                                    <span className="sr-only">Loading...</span>
                                </div></div> : null}
                                {this.state.step === 0 ? <Registration context={this} /> : null}
                                {this.state.step === 1 ? <RegistrationResult context={this} /> : null}
                            </div>
                        </div>
                    </div>
                </div>
                <Footer />
            </>
        );
    }
}
