import React, { Component } from 'react';
import { HashLink } from 'react-router-hash-link';
  
export default class FooterList extends Component {

  render() {
    return (
      <div className={this.props.context.class_}>
          <div className="single-footer">
              <div className="footer-title">
                  <h6>{this.props.context.title}</h6>
                </div>
                <ul className="footer-list">
                {
                    this.props.context.items.map((link_, index) => (
                      <li key={index}><HashLink to={link_.url} smooth>{link_.name}</HashLink></li>
                    ))
                }
                </ul>
          </div>
      </div>
    );
  }
};