import React, { Component } from 'react';
import Navigation from '../components/Navigation';
import Footer from "../components/Footer";
import Article from "../components/Article";
import {dynamicTitle, setDescription} from '../components/Helper'

export default class Privacy extends Component {

  componentDidMount = () => {
    dynamicTitle( {
      title: 'Refund Policy'
    } );
    setDescription( "Refund Policy" )
  }

  state = {
    title: "Refund Policy",
    lines: []
  }

  render() {
    return (
      <>
        <Navigation />
        <Article context={this.state}>
          <div className='privacy'>
            <h4>For Annual Plans </h4>
            <p>We offer a full money-back guarantee for all purchases of annual plans made on our website. 
               If you are not satisfied with the product that you have purchased from us, you can get your
               money back no questions asked. You are eligible for a full reimbursement within 15 calendar days of your
               purchase.</p>
            <p>After 15 days, you will no longer be eligible for a 100% refund. However, you will get partial refund which is calculated 
               using the number of months our service is used by you. The refund amount will be calculated as follows :</p>
            <p><b><i>Refund Amount = Total Amount Paid - ( Number of Months x Monthly Subscription Fee ) - Cost of availed Offers</i></b><br/></p>
            <p>It's worth noting that a partial month is charged in full while calculating the number of months. 
               100 days of service, for example, will be counted as four months.</p>
          </div>
          <h4>For Monthly Plans </h4>
          <p>You have the option to cancel your subscription at any time. Please keep in mind that you will not be eligible for a refund if you use this mode of payment.</p>
        </Article>
        <Footer />
      </>
    );
  }
}